@font-face {
  font-family: "ArchivoBlack-Regular";
  src: local("ArchivoBlack-Regular"),
    url("./assets/ArchivoBlack-Regular.ttf") format("truetype");
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

@font-face {
  font-family: "BungeeShade-Regular";
  src: local("BungeeShade-Regular"),
    url("./assets/BungeeShade-Regular.ttf") format("truetype");
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

body {
  margin: 0;
  font-family: "ArchivoBlack-Regular", cursive, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 1.4em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

.tier-jackpots {
  font-family: "BungeeShade-Regular";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
