  font-size: 0.7em;
}

h4 {
  color: cyan;
}

@media screen and (min-width: 0px) {
  .centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);
  }
}

@media screen and (min-width: 992px) {
  .centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-30%, -40%);
  }
  .App {
    padding-left: 100px;
    padding-right: 100px;
  }
}
.lottery {
